export default {
	created() {
		this.content = this.questionlist[this.number-1].content;
		this.choices = this.questionlist[this.number-1].choices;
	},
	data() {
		return {
			number: 1,
			content: '',
			choices: [],
			isActive: 99,
			questionlist: [
				{
					number: 1,
					content: '1233211234567'
				},
				{
					number: 2,
					content: 'Magisk_Q 5EPL的王'
				}
			]
		}
	},
	methods: {
		activeChoice (e){
			this.isActive = e
		},
		preQuestion (){
			if(this.number == 1){
				return
			}else{
				this.content = this.questionlist[this.number-2].content;
				this.choices = this.questionlist[this.number-2].choices;
				this.number--;
				this.isActive = 99;
			}
		},
		nextQuestion (){
			this.content = this.questionlist[this.number].content;
			this.choices = this.questionlist[this.number].choices;
			this.number++;
			this.isActive = 99;
		}
	}
}
